.hotel{
  position: relative;
  float: left;
  width: 100%;
  background-color: #fff;
  &__wrapper{
    position: relative;
    float: left;
    width: 100%;
    margin-bottom: 80px;
    &--element{
      position: relative;
      float: left;
      width: 100%;
      margin-bottom: 30px;
      padding-bottom: 30px;
      border-bottom: 1px solid #ececec;
    }
    &--slider{
      position: relative;
      float: left;
      width: 306px;
      height: 180px;
      overflow: hidden;
      &:hover{
        .slick-prev{
          left: 0;
          @include transition(0.4s);
        }
        .slick-next{
          right: 0;
          @include transition(0.4s);
        }
      }
      .slick-prev {
        left: -40px;
        width: 10%;
        height: 100%;
        z-index: 1;
        @include transition(0.4s);
        &:before{
          display: block;
          position: absolute;
          content: '';
          width: 36px;
          height: 43px;
          top: 67px;
          background-image: url('../img/icons/prev.png');
          background-repeat: no-repeat;
          -webkit-background-size: 37px 40px;
          background-size: 37px 40px;
          color: transparent;
        }
      }
      .slick-next {
        right: -40px;
        width: 10%;
        height: 100%;
        z-index: 1;
        @include transition(0.4s);
        &:before{
          display: block;
          position: absolute;
          content: '';
          width: 36px;
          height: 43px;
          top: 67px;
          background-image: url('../img/icons/next.png');
          background-repeat: no-repeat;
          -webkit-background-size: 37px 40px;
          background-size: 37px 40px;
          color: transparent;
        }
      }
    }
    &--photo{
      position: relative;
      float: left;
      width: 100%;
      height: 100%;
      overflow: hidden;
      @include breakpoint(max-width 307px){
        width: 100%;
        margin-bottom: 10px;
      }
      img, svg{
        size: 100%;
        object-fit: cover;
      }
    }
    &--about{
      position: relative;
      float: left;
      width: calc(100% - 306px);
      width: -moz-calc(100% - 306px);
      padding-left: 45px;
      @include box-sizing(border-box);
      @include breakpoint(max-width 660px){
        width: 100%;
        padding-left: 10px;
      }
      @include breakpoint(max-width 307px){
        width: 100%;
        margin-bottom: 10px;
      }
      > h3{
        position: relative;
        float: left;
        width: 100%;
        margin-top: 17px;
        margin-bottom: 54px;
        color: #00365f;
        font-size: 24px;
        @include breakpoint(max-width 660px){
          margin-top: 10px;
          margin-bottom: 10px;
        }
      }
      > p{
        position: relative;
        float: left;
        width: 100%;
        margin-top: 0;
        margin-bottom: 40px;
        color: #a2a2a2;
        font-size: 14px;
        line-height: 20px;
        @include breakpoint(max-width 660px){
          margin-bottom: 10px;
        }
      }
    }
    &--val{
      @include display-flex(flex);
      @include flex-direction(row);
      @include flex-wrap(wrap);
      @include justify-content(flex-start);
      position: relative;
      float: left;
      width: 100%;
      @include breakpoint(max-width 660px){
        @include flex-direction(column);
      }
    }
    &--item{
      position: relative;
      float: left;
      margin-right: 40px;
      color: #00365f;
      font-size: 12px;
      text-transform: uppercase;
      > span{
        color: #9a9a9a;
        margin-left: 4px;
      }
    }
    &--box{
      position: relative;
      float: left;
      width: 100%;
      margin-bottom: 60px;
      padding-bottom: 30px;
      @include box-sizing(border-box);
      border-bottom: 1px solid #ececec;
      > p{
        position: relative;
        float: left;
        width: 100%;
        margin-top: 0;
        margin-bottom: 0;
        text-align: center;
        color: #00365f;
        font-size: 18px;
        line-height: 25px;
      }
    }
  }
}